@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

*{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: default;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: rgb(48, 0, 64);
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

h2, h3, h4, h5{
  color: rgb(69, 245, 226);
  font-weight: 300;
}

h2{
  font-weight: 300;
  letter-spacing: 1rem;
  font-size: 1rem;
  margin: 2rem 0px;
}

h3{
  font-size: 0.9rem;
  letter-spacing: 2px;
  max-width: 420px;
  margin: 3rem auto;
}

h4{
  font-size: 0.9rem;
  margin: 2rem 0px 0px 0px;
}

h5{
  margin: 5px 0px;
}

ul{
  margin: 4rem 0px;
  padding: 0px;
}

li{
  display: inline-block;
  margin: 0 30px;
}

li a{
  color: rgb(249, 66, 229);
  text-decoration: none;
  display: inline-block;
}





