.App {
  text-align: center;
}

.Logo{
  width: 40%;
}

.Logos{
  margin: 3rem auto;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  
}

.Logos img{
  margin: 0px 20px;
  width: 40px;
}

.LargeLogo{
  width: 100px !important;
}

@media only screen and (max-width: 600px) {
  .Logo{
    width: 80%;
  }
  .Logos {
    display: block;
    text-align: center;
    margin: 4rem 0px;
  }
  .Logos img{
    display: block;
    margin: 0 auto;
  }
  li{
    margin: 0 12px 0px 0px;
  }
}


